import React from 'react'
import ReactMarkdown from 'react-markdown'
import people from '../content/output/people.json'

let types = [
  { key: 'faculty', title: 'Faculty' },
  { key: 'postdoc', title: 'Post Doc' },
  { key: 'phd', title: 'PhD' },
  { key: 'master', title: 'Master' },
  { key: 'undergrad', title: 'Undergrad'},
  { key: 'alumni', title: 'Alumni' }
]

class People extends React.Component {
  componentDidMount() {
  }

  render() {
    if (this.props.show) {
      types = types.filter((type) => type.key === 'faculty' )
    }
    return (
      <div id="people" className="category">
        <h1>People</h1>
        { types.map((type) => {
          return (
            <div className="people-category">
              <h2>{ type.title }</h2>
              <div className="ui grid">
                { people
                  .filter((person) => {
                    return person.type === type.key
                  }) // filter
                  .map((person) => {
                    return (
                      <div className="three wide column person">
                        <img className="ui mini circular image" src="/static/images/logo-3.png"/>
                        <p><b>{ person.name }</b></p>
                        <p>{ person.title }</p>
                      </div>
                    ) // return
                  }) // map
                }
              </div>
            </div>
          )
        })}
      </div>
    )
  }
}

export default People
